import downloadjs from 'downloadjs';
import html2canvas from 'html2canvas';
import React from "react"
import { useEffect } from "react"
import { useCallback } from 'react';
import { fabric } from 'fabric';
import { saveAs } from 'file-saver';
import download from './download.jpg';
import { Component } from 'react';
import * as htmlToImage from 'html-to-image';
import { toPng, toJpeg, toBlob, toPixelData, toSvg } from 'html-to-image';
import { useState } from 'react';
export default function Meme() {
 
   
 

    const [meme, setMeme] = React.useState({
        topText: "",
        bottomText: "",
        randomImage: "http://i.imgflip.com/1bij.jpg" 
    })
    const [allMemes, setAllMemes] = React.useState([])
    
    React.useEffect(() => {
        fetch("https://api.imgflip.com/get_memes")
            .then(res => res.json())
            .then(data => setAllMemes(data.data.memes))
    }, [])
    
 
    
    function getMemeImage() {
         
        const randomNumber = Math.floor(Math.random() * allMemes.length)
        const url = allMemes[randomNumber].url
        setMeme(prevMeme => ({
            ...prevMeme,
            randomImage: url
        }))
        
    }
    
    function handleChange(event) {
        const {name, value} = event.target
        setMeme(prevMeme => ({
            ...prevMeme,
            [name]: value
        }))
    }


    const cli = useCallback(async () => {
     
        const meme = meme.randomImage;
        
        const canvas = await(meme);
        const dataURL = canvas.toDataURL('image/png');
        downloadjs(dataURL, 'download.png', 'image/png');
      }, []);
  
   const mem ='https://api.imgflip.com/get_memes';

    const clic = useCallback(async () => {
     
        
        const m =  meme.randomImage;
        const canvas =  await  html2canvas(meme.randomImage ? document.body :  document);
        const dataURL = canvas.toDataURL('image/jpg');
        downloadjs(dataURL, 'download.jpg', 'image/jpg');
      });
  


    function handleCaptureClick(){
      
        var node = document.getElementById('imagewrap');

        htmlToImage.toJpeg(document.getElementById('imagewrap'), { quality: 0.95 })
        .then(function (dataUrl) {
          var link = document.createElement('a');
          link.download = `${ meme.topText + '_' + meme.bottomText}` ;
          link.href = dataUrl;
          link.click();
        });

 
}

    return (
        <main>
           <h1 className='idk' style={{'text-align' :'center'}}> Click Text To reposition!</h1>
            <div  className="form" >
                <input 
                    type="text"
                    placeholder="Top text"
                    className="form--input"
                    name="topText"
                    value={meme.topText}
                    onChange={handleChange}
                />
                <input 
                    type="text"
                    placeholder="Bottom text"
                    className="form--input"
                    name="bottomText"
                    value={meme.bottomText}
                    onChange={handleChange}
                />
                <button 
                    className="form--button"
                    onClick={getMemeImage}
                >
                    Get a new meme image 🖼
                </button>
            </div>

            <div  id="imagewrap" className="meme">
            <img  src={meme.randomImage} id="pics" className="meme--image" alt={meme.randomImage} />
             <div id="mydiv">  <div  id="mydivheader" className="absolute meme--text top">{meme.topText}</div> </div>
             <div id="mydivi">  <div id="mydivheaderi" className="absolute meme--text bottom">{meme.bottomText}</div> </div>


            </div>
            <button className='bt' onClick={handleCaptureClick} >Download Meme</button>

        </main>

      
)

}


